import React from "react";
import "../App.css";
class Searchbar extends React.Component{
    state={
        term:''
    }

    searchhandler=(event)=>
    {
        this.setState({
            term:event.target.value
        })
    }

    submithandler=(event)=>{
        event.preventDefault();
        this.props.ontermsubmit(this.state.term)
    }
    render()
    {
        return(
            <form onSubmit={this.submithandler}>
        <input placeholder="Search..." type="text" className="searchbar" value={this.state.term} onChange={this.searchhandler}>
        
        </input>
        <svg className="searchsvgpos" width="78" height="62" viewBox="0 0 78 62" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M50.375 36.1667H47.8075L46.8975 35.4692C50.0825 32.5242 52 28.7008 52 24.5417C52 15.2675 42.5425 7.75 30.875 7.75C19.2075 7.75 9.75 15.2675 9.75 24.5417C9.75 33.8158 19.2075 41.3333 30.875 41.3333C36.1075 41.3333 40.9175 39.8092 44.6225 37.2775L45.5 38.0008V40.0417L61.75 52.9325L66.5925 49.0833L50.375 36.1667ZM30.875 36.1667C22.7825 36.1667 16.25 30.9742 16.25 24.5417C16.25 18.1092 22.7825 12.9167 30.875 12.9167C38.9675 12.9167 45.5 18.1092 45.5 24.5417C45.5 30.9742 38.9675 36.1667 30.875 36.1667Z" fill="#8E8E8E"/>
            </svg> 
        </form>
        );
    }
};

    export default Searchbar;