import React from "react";
import "../App.css"

const Result=props=>{
    //let images;

    //const {gifs}= props;//destructuring
    //console.log(props.gifs.data)
    let newarray=props.gifs.data;
    if(!newarray)
    {
        return <div>Enter query...</div>;
    }
    else
    {
    let images=newarray.map(item=>{
        //console.log(item.images)
        return <img className="border border-primary" alt={item.images.fixed_height_small.url} style={{padding: "0.5rem 1rem",borderradius: "4px"}} key={item.images.downsized_medium.url} src={item.images.downsized_medium.url}></img>; 
        }
    )

    return(
        <div className="containbox">
            {images}  
        </div>
    );
    }

   
}

export default Result;